html {
  font-family: "sans-serif", "Open Sans";
  background-color: white;
}
*,
::after,
::before {
  box-sizing: border-box;
}
body {
  font-family: sans-serif;
  min-width: 960px;
  min-height: 100%;
  margin: 0;
  padding: 0;
  font-family: Cuprum, Tahoma, sans-serif;
  display: grid;
  align-content: start;
}
